%placeholder {
  top: 110%;
  position: absolute;
  content: 'Please choose a user';
  display: block;
  color: red;
  padding: 5px 20px;
  background-color: pink;
  border-radius: 5px;  
}

.App {
  height: 100vh;
  background: radial-gradient(circle, rgba(34,193,195,1) 0%, rgba(233,190,96,1) 100%);
}
.todo {
  &__user-container {
    position: relative;

    &.invalid::after {
      @extend %placeholder;
    }
  }

  &__title-container {
    position: relative;

    &.invalid::after {
      @extend %placeholder;
    }
  }
}
